.popup {
  cursor: pointer;
  -webkit-user-select: none;
  user-select: none;
  display: block;
  position: relative;
  top: 48px;
  right: 45%;
  overflow: visible;
}

.popup .popuptext {
  visibility: hidden;
  opacity: .99;
  width: 160px;
  color: #fff;
  text-align: center;
  z-index: 1;
  background-color: #555;
  border-radius: 6px;
  margin-left: -80px;
  padding: 8px 0;
  position: absolute;
  bottom: 125%;
  left: 50%;
  overflow: visible;
}

.popup .popuptext:after {
  content: "";
  border: 5px solid #0000;
  border-bottom-color: #555;
  margin-left: -5px;
  position: absolute;
  bottom: 100%;
  left: 50%;
}

.popup .show {
  visibility: visible;
  animation: 1s fadeIn;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
